"use strict";
exports.__esModule = true;
exports.AEXECEL = void 0;
var constants_1 = require("./utils/constants");
var AEXECEL;
(function (AEXECEL) {
    'use strict';
    var AExecel = /** @class */ (function () {
        function AExecel(fileName) {
            this.fileName = fileName || (+new Date()).toString(); //默认时间戳为文件名
            // 检查系统有没有ActiveXObject
            // ActiveXObject ? this.hasActiveXObject = true : this.hasActiveXObject = false
        }
        //检查并获取用户的自定义样式
        AExecel.prototype.getCustomerStyle = function (customerObject) {
            var customerStyleStr = Object.keys(customerObject).map(function (key) { return key + ":" + customerObject[key]; }).join(',');
            return customerStyleStr === '{}' ? '' : customerStyleStr.slice(1, customerStyleStr.length - 1);
        };
        /**
         * @typedef a链接导出
         * @param fileName  文件名称
         * @param linkUrl   链接
         * @param fileExtension   文件后缀
         */
        AExecel.prototype.createLink = function (linkUrl, fileName, fileExtension) {
            fileExtension = fileExtension || constants_1.FileType.xml; //默认类型为xml
            var aTag = document.createElement('a');
            aTag.download = (fileName || this.fileName) + "." + fileExtension;
            document.body.appendChild(aTag);
            aTag.href = linkUrl;
            aTag.click();
            document.body.removeChild(aTag);
        };
        /**
         *
         * 将table转化为excel导出
         * 优点：转化为table，比较方便,可以设置背景色等
         */
        AExecel.prototype.createByTable = function (titleArray, jsonData) {
            var utf8Heading = '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8">';
            // table的需要模板
            var template = {
                head: '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">' + utf8Heading + '<head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets>',
                sheet: {
                    head: '<x:ExcelWorksheet><x:Name>',
                    tail: '</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet>'
                },
                mid: '</x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body>',
                table: {
                    head: '<table>',
                    tail: '</table>'
                },
                foot: '</body></html>'
            };
            var tableRows = []; //放数据的地方
            // let computedStyle = {}//计算过后的样式对象
            var row = '';
            // 需要把头的也遍历一边，后面需要拼接
            // row += `<tr style=' ${computedStyle} '>`
            row += '<tr>';
            // 竖排
            titleArray.forEach(function (td) {
                row += "<td>" + td + "</td>";
            });
            row += '</tr>';
            // 每一列都加上去
            tableRows.push(row);
            // 便利获取到样式以及元素的k-v，拼接起来
            // 横排
            jsonData.forEach(function (tr) {
                row = '<tr>';
                // 竖排
                Object.keys(tr).forEach(function (td) {
                    row += "<td>" + tr[td] + "</td>";
                });
                row += '</tr>';
                // 每一列都加上去
                tableRows.push(row);
            });
            // 最后完整的字符串内容
            var fullTemplateStr = '';
            //拼接整个头部
            fullTemplateStr += template.head;
            // 增加sheet的头部
            fullTemplateStr += template.sheet.head;
            // 加上内容
            fullTemplateStr += 'sheet1';
            // 增加sheet的尾部
            fullTemplateStr += template.sheet.tail;
            // 增加上中间的部分
            fullTemplateStr += template.mid;
            fullTemplateStr += template.table.head;
            // 加上正式内容
            tableRows.forEach(function (row) {
                fullTemplateStr += row;
            });
            fullTemplateStr += template.table.tail;
            //拼接整个尾部
            fullTemplateStr += template.foot;
            console.log('最后拼接的数据是:');
            console.log(fullTemplateStr);
            // 封装成为一个sheet
            var blob = new Blob([fullTemplateStr], { type: 'application/vnd.ms-excel' });
            //解决中文乱码问题
            blob = new Blob([String.fromCharCode(0xFEFF), blob], { type: blob.type });
            this.createLink(window.URL.createObjectURL(blob), this.fileName, constants_1.FileType.xls);
            // 遍历数组，获取k-v，
        };
        /**
        以blob流的形式，组合成csv格式导出
         * 缺点：暂时不知道怎么多个sheet
         * 优点：文件比较大的时候没问题
         */
        AExecel.prototype.createLargerCsv = function (titleArray, jsonData) {
            var str = '';
            var blob;
            this.startTime = new Date();
            if (this.hasActiveXObject) {
                // this.createByAX(titleArray, jsonData)
                return;
            }
            // 使用string是因为如果是0的话会不显示，
            titleArray.forEach(function (title) {
                str += title.toString() + ",";
            });
            str += '\n';
            jsonData.forEach(function (data) {
                Object.values(data).forEach(function (o) {
                    str += o.toString() + ",";
                });
                str += '\n';
            });
            // 添加编码格式
            blob = new Blob([str], { type: 'text/plain;charset=utf-8' });
            //解决中文乱码问题
            blob = new Blob([String.fromCharCode(0xFEFF), blob], { type: blob.type });
            this.createLink(window.URL.createObjectURL(blob), this.fileName, constants_1.FileType.csv);
            this.endTime = new Date();
            // 返回开始，结束时间
            return {
                endTime: this.endTime,
                startTime: this.startTime
            };
        };
        return AExecel;
    }());
    AEXECEL.AExecel = AExecel;
})(AEXECEL = exports.AEXECEL || (exports.AEXECEL = {}));
